<template>
  <div class="df ffcn aic">
    <div class="df ffrw jcsb mt10" style="width:95vw;">
      <ui-button @click="htmlchange(html1,1)" style="background:var(--gradualRed)">属性选择器</ui-button>
      <ui-button @click="htmlchange(html2,2)" style="background:var(--gradualOrange)">属性和值选择器</ui-button>
      <ui-button @click="htmlchange(html3,3)" style="background:var(--gradualGreen)">属性和值选择器-多个值1</ui-button>
      <ui-button @click="htmlchange(html4,4)" style="background:var(--gradualPurple)">属性和值选择器-多个值2</ui-button>
      <ui-button @click="htmlchange(html5,5)" style="background:var(--gradualPink)">设置表单的样式</ui-button>
    </div>
    <css-code v-if="hackReset" :html="html"></css-code>
  </div>
</template>

<script>
import { UiButton } from "keen-ui";
import CssCode from "@/components/CSS/code.vue";
export default {
  name: "css_syntax_attribute_selector",
  components: {
    CssCode
  },
  data() {
    return {
      index: 1,
      hackReset: true,
      html: [],
      html1: [
        "<!--对带有指定属性的 HTML 元素设置样式。",
        "可以为拥有指定属性的 HTML 元素设置样式，而不仅限于 class 和 id 属性。",
        "注释：只有在规定了 !DOCTYPE 时，IE7 和 IE8 才支持属性选择器。在 IE6 及更低的版本中，不支持属性选择。",
        " ",
        "属性选择器",
        "下面的例子为带有 title 属性的所有元素设置样式：",
        "--->",
        "<!DOCTYPE html PUBLIC '-//W3C//DTD XHTML 1.0 Transitional//EN' 'http://www.w3.org/TR/xhtml1/DTD/xhtml1-transitional.dtd'>",
        "<html>",
        "<head>",
        "<style type='text/css'>",
        "[title]",
        "{",
        "  color:red;",
        "}",
        "</style>",
        "</head>",
        "<body>",
        "<h1>可以应用样式：</h1>",
        "<h2 title='Hello world'>Hello world</h2>",
        "<a title='W3School' href='http://w3school.com.cn'>W3School</a>",
        "<hr />",
        "<h1>无法应用样式：</h1>",
        "<h2>Hello world</h2>",
        "<a href='http://w3school.com.cn'>W3School</a>",
        "</body>",
        "</html>"
      ],
      html2: [
        "<!--属性和值选择器",
        "下面的例子为 title='W3School' 的所有元素设置样式：",
        "-->",
        "<!DOCTYPE html PUBLIC '-//W3C//DTD XHTML 1.0 Transitional//EN' 'http://www.w3.org/TR/xhtml1/DTD/xhtml1-transitional.dtd'>",
        "<html>",
        "<head>",
        "<style type='text/css'>",
        "[title=W3School]",
        "{",
        "  border:5px solid blue;",
        "}",
        "</style>",
        "</head>",
        " ",
        "<body>",
        "<h1>可以应用样式：</h1>",
        "<img title='W3School' src='https://www.w3school.com.cn/i/w3school_logo_white.gif' />",
        "<br />",
        "<a title='W3School' href='http://w3school.com.cn'>W3School</a>",
        "<hr />",
        " ",
        "<h1>无法应用样式：</h1>",
        "<p title='greeting'>Hi!</p>",
        "<a class='W3School' href='http://w3school.com.cn'>W3School</a>",
        "</body>",
        "</html>"
      ],
      html3: [
        "<!--",
        "属性和值选择器 - 多个值",
        "下面的例子为包含指定值的 title 属性的所有元素设置样式。适用于由空格分隔的属性值：",
        "-->",
        "<html>",
        "<head>",
        "<style type='text/css'>",
        "[title~=hello]",
        "{",
        "  color:red;",
        "}",
        "</style>",
        "</head>",
        " ",
        "<body>",
        "<h1>可以应用样式：</h1>",
        "<h2 title='hello world'>Hello world</h2>",
        "<p title='student hello'>Hello W3School students!</h1>",
        "<hr />",
        " ",
        "<h1>无法应用样式：</h1>",
        "<h2 title='world'>Hello world</h2>",
        "<p title='student'>Hello W3School students!</p>",
        "</body>",
        "</hmtl>"
      ],
      html4: [
        "<!--",
        "下面的例子为带有包含指定值的 lang 属性的所有元素设置样式。适用于由连字符分隔的属性值：",
        "-->",
        "<html>",
        "<head>",
        "<style type='text/css'>",
        "[lang|=en]",
        "{",
        "  color:red;",
        "}",
        "</style>",
        "</head>",
        "",
        "<body>",
        "<h1>可以应用样式：</h1>",
        "<p lang='en'>Hello!</p>",
        "<p lang='en-us'>Hi!</p>",
        "<hr />",
        " ",
        "<h1>无法应用样式：</h1>",
        "<p lang='us'>Hi!</p>",
        "<p lang='zh'>Hao!</p>",
        "</body>",
        "</html>"
      ],
      html5: [
        "<!--",
        "设置表单的样式",
        "属性选择器在为不带有 class 或 id 的表单设置样式时特别有用：",
        "input[type='text']",
        "{",
        "  width:150px;",
        "  display:block;",
        "  margin-bottom:10px;",
        "  background-color:yellow;",
        "  font-family: Verdana, Arial;",
        "}",
        " ",
        "input[type='button']",
        "{",
        "  width:120px;",
        "  margin-left:35px;",
        "  display:block;",
        "  font-family: Verdana, Arial;",
        "}",
        "-->",
        "",
        "<html>",
        "<head>",
        "<style>",
        "input[type='text']",
        "{",
        "width:150px;",
        "display:block;",
        "margin-bottom:10px;",
        "background-color:yellow;",
        "font-family: Verdana, Arial;",
        "}",
        " ",
        "input[type='button']",
        "{",
        "width:120px;",
        "margin-left:35px;",
        "display:block;",
        "font-family: Verdana, Arial;",
        "}",
        "</style>",
        "</head>",
        "<body>",
        " ",
        "<form name='input' action='' method='get'>",
        "<input type='text' name='Name' value='Bill' size='20'>",
        "<input type='text' name='Name' value='Gates' size='20'>",
        "<input type='button' value='Example Button'>",
        " ",
        "</form>",
        "</body>",
        "</html>"
      ]
    };
  },
  mounted() {
    this.html = this.html1;
  },
  methods: {
    htmlchange(html, index) {
      if (this.index == index) return;
      this.hackReset = false;
      this.$nextTick(() => {
        this.hackReset = true;
        this.$nextTick(() => {
          this.index = index;
          this.html = html;
        });
      });
    }
  }
};
</script>

<style scoped>

</style>



